</div>

<style>
    .confirm{
        width: 100%;
        height: 100%;
    }
    .box{
        position:relative
    }

    .content-icon{
        height: 100px;
        width: 100px;
        text-align: center;
        line-height: 120px;

    }
    .toolbar {
        float:left;
        border-radius: 4px;
        background-color:#828282;
    }

    .toolbar button {
        display: block;
        padding: 10px;
        width: 55px;
        border: none;
        border-radius: 4px;
        text-align: center;
        font-size: 22px;
        color: white;
        background-color:#828282;
    }

    .toolbar button:hover {
        background-color: #3850b7;
        cursor: pointer;
