<script  lang="ts">
    import FormApi from "gold-admin/form/form-api";
    import toast from "gold-admin/toast";
    import Form from "gold-admin/form/form";
    import {getModal} from "src/ui/map/map-modal.svelte";


    export let grid = [4, 4];

    $: col = `repeat(${grid[1]}, 1fr)`;
    $: row = `repeat(${grid[0]}, 1fr)`;
    $: is_active = Array(grid[0]).fill(0).map(_ => Array(grid[1]).fill(false));

    $: floors = Array(grid[0]).fill(0).map(_ => Array(grid[1]).fill(
        null
     ));
    $: objects = Array(grid[0]).fill(0).map(_ => Array(grid[1]).fill(
        null
    ));
    $: pois = Array(grid[0]).fill(0).map(_ => Array(grid[1]).fill(
        null
    ));
    let start = [];
    let end = [];
    let hover_end = []
    let clicked = false;
    let w;
    let h;
    let is_active;
    let col;
    let row;

    export let id = 0;
    export let name = "";
    export let tile = "";
    export let layer = "";
    export let walkable = false;

    export let title = "";
    export let  body = "";
    export let type = "";
    export let color ="";
    export let floorClear;
    export let objectClear;
    export let poiClear;
    export let item;
    export let mapName;
    export let map

    let floors;
    let objects;
    let pois;
    let gridDivChild;
    let gridDiv;

    function selectStart(i, j) {
        clicked = true;
        start = [i, j];
        check_active([i, j]);
    }

    function selectEnd(i, j) {
        end = [i, j];
        check_active([i, j]);
        clicked = false;

        for (let i = 0; i < is_active.length; i++) {
            for (let j = 0; j < is_active[i].length; j++) {
                let objectDiv = document.createElement('div');
                let poiDiv = document.createElement('div');
                gridDivChild =  gridDiv.children[`${i}:${j}`];
                if (is_active[i][j]) {

                    if (layer == "floor"){
                        gridDivChild.style = `
                        background-image: url(${tile});
                        background-size: ${w/grid[1]}px ${h/grid[0]}px;
                        background-repeat: no-repeat;
                        `;

                        floors[i][j]= {"id":id, "tile":tile, "layer":layer, "walkable":walkable};

                        is_active[i][j]=false;
                    }
                    if (floorClear){
                        gridDivChild.style ='';
                        floors[i][j] = null;
                        is_active[i][j]=false;
                    }
                    if (layer == "object" ){
                        if (!gridDivChild.hasChildNodes()){
                            gridDivChild.appendChild(objectDiv)
                        }
                        gridDivChild.children[0].className = "";
                        gridDivChild.children[0].style = `
                        background-image: url(${tile});
                        background-size: ${w / grid[1]}px ${h / grid[0]}px;
                        background-repeat: no-repeat;
                        width: 100%;
                        height:100%;
                        `;

                        objects[i][j] = {"id":id, "tile":tile, "layer":layer, "walkable":walkable};

                        is_active[i][j] = false;
                    }

                    if (objectClear){
                        if (gridDivChild.hasChildNodes() && !gridDivChild.children[0].hasChildNodes()){
                            gridDivChild.children[0].remove();
                        }
                        if (gridDivChild.hasChildNodes() && gridDivChild.children[0].hasChildNodes()){
                            gridDivChild.children[0].style ='';
                        }
                        objects[i][j]  = null;
                        is_active[i][j]=false;
                    }

                    if(type != '' && layer != "object" && layer != "floor"){

                        if (!gridDivChild.hasChildNodes()){
                            gridDivChild.appendChild(objectDiv)
                            gridDivChild.children[0].appendChild(poiDiv)
                        }
                        if (gridDivChild.hasChildNodes()){
                            gridDivChild.children[0].appendChild(poiDiv)
                        }
                        gridDivChild.children[0].children[0].style = ``;
                        gridDivChild.children[0].children[0].className = "fas fa-map-marker-alt";
                        gridDivChild.children[0].children[0].style = `
                         font-size: ${h / grid[0]/2.5}px;
                         color:${color};
                         width: 100%;
                         height:100%;
                         text-align: center;
                         line-height: ${h / grid[0]}px;

                        `;

                        pois[i][j] = {"id":id, "title":title, "body":body, "type":type};
                        is_active[i][j] = false;
                    }
                    if (poiClear ){
                        if (gridDivChild.hasChildNodes()){
                            if (gridDivChild.children[0].hasChildNodes()){
                                gridDivChild.children[0].children[0].remove();
                            }
                        }
                        pois[i][j]  = null;
                        is_active[i][j]=false;
                    }
                }
            }
        }
    }

    function hover(i, j) {
        if (!clicked) return;
        hover_end = [i, j];
        check_active(hover_end);
    }

    function is_in_range([i, j], [i2, j2]) {
        return ((i - start[0]) * (i - i2) <= 0) &&
            ((j - start[1]) * (j - j2)<= 0)
    }

     function check_active (end) {
        is_active = is_active.map(
            (a, i) => a.map((_, j) => is_in_range([i, j], end)));
    }

   export function loadMap(){
       for (let i = 0; i < map.floors.length; i++) {
           for (let j = 0; j < map.floors[i].length; j++) {
               let objectDiv = document.createElement('div');
               let poiDiv = document.createElement('div');
               gridDivChild =  gridDiv.children[`${i}:${j}`];

               if(map.floors[i][j] != null){
                   gridDivChild.style = `
                        background-image: url(${map.floors[i][j].tile});
                        background-size: ${w/grid[1]}px ${h/grid[0]}px;
                        background-repeat: no-repeat;
                        `;
               }

               if(map.floors[i][j] == null){
                   gridDivChild.style ='';
               }

               if (map.objects[i][j] != null){
                   if (!gridDivChild.hasChildNodes()){
                       gridDivChild.appendChild(objectDiv)
                   }
                   gridDivChild.children[0].className = "";
                   gridDivChild.children[0].style = `
                        background-image: url(${map.objects[i][j].tile});
                        background-size: ${w / grid[1]}px ${h / grid[0]}px;
                        background-repeat: no-repeat;
                        width: 100%;
                        height:100%;
                        `;
               }

               if (map.objects[i][j] == null && gridDivChild.hasChildNodes()){
                   gridDivChild.children[0].remove();
               }

               if(map.pois[i][j] != null){
                   map.pois[i][j].type == "teleport" ? color = "brown" : color = "orange";
                   if (!gridDivChild.hasChildNodes()){
                       gridDivChild.appendChild(objectDiv)
                       gridDivChild.children[0].appendChild(poiDiv)
                   }
                   if (gridDivChild.hasChildNodes()){
                       gridDivChild.children[0].appendChild(poiDiv)
                   }
                   gridDivChild.children[0].children[0].style = ``;
                   gridDivChild.children[0].children[0].className = "fas fa-map-marker-alt";
                   gridDivChild.children[0].children[0].style = `
                         font-size: ${h / grid[0]/2.5}px;
                         color:${color};
                         width: 100%;
                         height:100%;
                         text-align: center;
                         line-height: ${h / grid[0]}px;
                        `;
               }

               if (map.pois[i][j] == null ){
                   if (gridDivChild.hasChildNodes()){
                       if (gridDivChild.children[0].hasChildNodes()){
                           gridDivChild.children[0].children[0].remove();
                       }
                   }
               }
           }
       }
   }

    export async function saveAll(){
        Form.api = new FormApi('/api/map/item/');

        item.name = mapName;
        item.map = {
            floors,
            objects,
            pois
        };
        if( item.name != "" ){
            try {

                let id = await Form.api!.create(item);
                if (typeof id === "number") item.id = id;
                toast.success("Map saved");

            } catch (e: any) {
                if (e.code === 422) this.errors = e.messages;
            }
            getModal("save").close();
        }else {
            toast.danger("Map name is required");
        }
    }



</script>

<div    bind:clientWidth={w} bind:clientHeight={h}
        bind:this = {gridDiv}
        class="container"
        style="grid-template-rows: {row}; grid-template-columns: {col};"
>

        {#each { length: grid[0] } as _, i (i)}
            {#each { length: grid[1] } as _, j (j)}
                <div class:active={is_active[i][j]} id="{i}:{j}"
                     on:mouseover={() => hover(i, j)}
                     on:mousedown={() => selectStart(i, j)}
                     on:mouseup={() => selectEnd(i, j)}
                />
            {/each}
        {/each}


</div>

<style>
    .container {
        display: grid;
        border: 1px solid #999;
        border-radius: 2px;
        width: 1200px;
        height: 700px;
        grid-gap: 1px;
        background: #999;
        -webkit-user-drag: none;
        user-select: none;
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
    }

    .container div {
        background: #fff;
    }

    .container div:hover {
        opacity: 0.5;
        cursor: pointer;
    }

    div.active {
        opacity: 0.5;
    }
    div.active:hover{
        background: #fff;
    }

</style>